import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import { formatTotalComps } from "../../lib";

const Property = (props) => (
  <div className="pipeline">
    <div
      className="panel mui-panel"
      onClick={props.navChange.bind(null, props.violations, props.name)}
    >
      <Row
        className="panel-heading"
        onClick={props.clickFunction.bind(
          this,
          props.dealId,
          props.violations,
          props.otherProperty,
          "violations"
        )}
      >
        <Col md="12">
          <p className="panel-info-value property-title">
            <b>{props.name}</b>
          </p>
        </Col>
      </Row>
      <Row
        onClick={props.clickFunction.bind(
          this,
          props.dealId,
          props.violations,
          props.otherProperty,
          "violations"
        )}
      >
        <Col md="2">
          <p className="small-title deal-info-title">Address</p>
          <p className="panel-info-value">{props.address}</p>
          <p className="panel-info-value">{props.zip}</p>
          <p className="panel-info-value">
            <i
              className="orange-text orange-text-hover pointer"
              onClick={props.clickFunction.bind(
                this,
                props.dealId,
                props.violations,
                props.otherProperty,
                "property-details"
              )}
            >
              {props.otherProperty === 0
                ? null
                : props.otherProperty === 1
                ? "1 other property"
                : props.otherProperty + " other properties"}
            </i>
          </p>
        </Col>
        <div>{props.info}</div>
      </Row>
      <Row
        onClick={props.clickFunction.bind(
          this,
          props.dealId,
          props.violations,
          props.otherProperty,
          "violations"
        )}
      >
        <Col md="12">
          <h3 className="quote-section orange-text">Loan details</h3>
          {props.loan}
        </Col>
      </Row>
      <Divider style={{ marginTop: "20px" }} />
      <Row>
        <Col md="12">
          <Row className="comps-row">
            <div
              onClick={props.compsClick.bind(
                null,
                props.inNYC,
                props.address,
                props.zip,
                props.property_type_id
              )}
            >
              <div>
                <div
                  className={props.violations !== null ? "border-right" : ""}
                >
                  <p className="violation-value comps">
                    {formatTotalComps(props.comps)}
                    <span>Comps</span>
                  </p>
                </div>
              </div>
            </div>
            <div
              className={
                props.violations !== null ? "comps-row-violations" : "hidden"
              }
              onClick={props.navChange.bind(null, props.violations, props.name)}
            >
              <div
                className="comps-row-violations"
                onClick={props.clickFunction.bind(
                  this,
                  props.dealId,
                  props.violations,
                  props.otherProperty,
                  "violations"
                )}
              >
                <div>
                  <p className="violation-value">
                    {props.violations}
                    <span>Violations</span>
                  </p>
                </div>
                <div>
                  <p className="violation-value">
                    {props.complaints}
                    <span>Complaints</span>
                  </p>
                </div>
              </div>
            </div>
          </Row>
        </Col>
      </Row>
    </div>
  </div>
);

export default Property;
