import * as types from "./actionTypes";
import config from "../config";
import { tokenExpired } from "./authenticationActions";
import { ajax } from "../lib";

export function loadInProcessPipeline() {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}inprocess-pipeline`,
      applyAuthHeader: true,
      success: function (response) {
        if (!response.isResponse) {
          dispatch(tokenExpired());
        } else {
          if (response.status === "204") {
            dispatch({ type: types.NO_IN_PROCESS_PIPELINE });
          } else {
            dispatch({
              type: types.LOAD_IN_PROCESS_PIPELINE,
              data: response.data.inProcessPipeline,
            });
          }
        }
      },
    });
  };
}

export function noViolationsFeedbackToast() {
  return { type: types.NO_VIOLATIONS_FEEDBACK_TOAST };
}

export function closeViolationsFeedbackToast() {
  return { type: types.CLOSE_VIOLATIONS_FEEDBACK_TOAST };
}
