import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as inProcessPipelineActions from "../../actions/inProcessPipelineActions";
import InProcessPipelinePage from "./InProcessPipelinePage";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as compsActions from "../../actions/compsActions";
import Expire from "../common/Expire";
import Button from "muicss/lib/react/button";

class InProcessPipelineContainer extends React.Component {
  constructor(props) {
    super(props);
    this.navChange = this.navChange.bind(this);
  }

  componentDidMount() {
    this.props.actions.loadInProcessPipeline();
    this.props.navActions.updateNavTitle("In Process Pipeline");
    if (
      this.props.comps.previousScrollPos &&
      this.props.comps.previousScrollPos.length
    ) {
      window.scrollTo(
        0,
        this.props.comps.previousScrollPos[
          this.props.comps.previousScrollPos.length - 1
        ]
      );
      this.props.compsActions.resetScrollPosition();
    }
  }

  navChange(violations, title) {
    if (violations >= 1) {
      this.props.navActions.updateNavTitle(title);
    } else {
      this.props.actions.noViolationsFeedbackToast();
    }
  }

  closeViolationsFeedbackToast() {
    this.props.actions.closeViolationsFeedbackToast();
  }

  render() {
    let successMessage = null;
    if (this.props.inProcessPipeline.toastShowing) {
      successMessage = (
        <Expire
          closeToast={this.closeViolationsFeedbackToast.bind(this)}
          delay={6000}
        >
          <div className="success-message">
            <p>No violation data available for this deal</p>
            <Button
              variant="flat"
              onClick={this.closeViolationsFeedbackToast.bind(this)}
            >
              Done
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <div>
        {successMessage}
        <InProcessPipelinePage
          inProcessDeals={this.props.inProcessPipeline.inProcessDeals}
          loading={this.props.inProcessPipeline.loading}
          noQuotes={this.props.inProcessPipeline.noQuotes}
          navChange={this.navChange}
          setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
          submitCompsSearch={this.props.compsActions.submitCompsSearch}
        />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    inProcessPipeline: state.inProcessPipeline,
    title: state.navTitle,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(inProcessPipelineActions, dispatch),
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    compsActions: bindActionCreators(compsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(InProcessPipelineContainer);
