import React from "react";
import Property from "./Property";
import PropertyinfoField from "./PropertyInfoField";
import Loaninfo from "./LoanInfoTitle";
import { Navigation } from "../../lib";
import Loader from "../common/Loader";
import encodeQueryData from "../common/encodeQueryData";

export default class InProcessPipelinePage extends React.Component {
  getLoanDetails(item) {
    let propertyInfo = [];
    propertyInfo.push(
      <Loaninfo key="0" title="Banker name" value={item.banker} />
    );
    propertyInfo.push(
      <Loaninfo key="1" title="Loan amount" value={item.quoted_amount} />
    );
    propertyInfo.push(<Loaninfo key="2" title="Rate" value={item.rate} />);
    propertyInfo.push(
      <Loaninfo
        key="3"
        title="Term / Amortization"
        value={item.term + " / " + item.amortization}
      />
    );
    return propertyInfo;
  }

  getPropertyInfo(item) {
    let propertyInfo = [];
    propertyInfo.push(
      <PropertyinfoField
        key="1"
        title="Expected closing date"
        value={item.ecl_dates}
      />
    );
    propertyInfo.push(
      <PropertyinfoField
        key="2"
        title="Main brokers"
        value={this.getBrokers(item)}
      />
    );
    propertyInfo.push(
      <PropertyinfoField
        key="3"
        title="Sr UW/processor"
        value={item.underWriterDF}
      />
    );
    propertyInfo.push(
      <PropertyinfoField
        key="4"
        title="Client name"
        value={item.primary_client}
      />
    );
    return propertyInfo;
  }

  dealClick(dealId, violations, additional_properties, page, e) {
    e.stopPropagation();
    if (violations != null || additional_properties > 0) {
      this.props.setPageAndScrollPos("/inProcessPipeline", window.scrollY);
      Navigation.go(
        `/IPPdeal-details/${dealId}/${page}?${encodeQueryData({
          violations,
          additional_properties,
        })}`
      );
    }
  }

  compsClick(inNYC, address, zip, property_type_id) {
    sessionStorage.setItem("comps_inNYC", inNYC);
    this.props.setPageAndScrollPos("/inProcessPipeline", window.scrollY);
    this.props.submitCompsSearch(`${address} ${zip}`, property_type_id);
  }

  getBrokers(item) {
    if (item.face_of_deal === null && item.broker_of_record === null) {
      return "N/A";
    }
    let brokers = "";
    if (item.broker_of_record) {
      brokers += item.broker_of_record;
      if (item.face_of_deal) {
        brokers += ", ";
      }
    }
    if (item.face_of_deal) {
      brokers += item.face_of_deal;
    }
    return brokers;
  }

  render() {
    const inProcess = this.props.inProcessDeals.map((item) => (
      <Property
        key={item.deal_id}
        property_type_id={item.property_type_id}
        dealId={item.deal_id}
        name={item.deal_name}
        address={item.property_address}
        zip={item.city_state_zip}
        otherProperty={item.CntOtherProperties}
        dealTitle={item.deal_name}
        loan={this.getLoanDetails(item)}
        info={this.getPropertyInfo(item)}
        clickFunction={this.dealClick.bind(this)}
        compsClick={this.compsClick.bind(this)}
        navChange={this.props.navChange}
        complaints={item.total_complaints}
        violations={item.total_violations}
        inNYC={item.inNYC}
        comps={item.TotalComps}
      />
    ));
    return (
      <div>
        {inProcess}
        {this.props.loading && <Loader />}
        <div className={this.props.noQuotes ? "" : "hidden"}>
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">No Deals In Process</h2>
          </div>
        </div>
      </div>
    );
  }
}
